<template>
  <v-container>
    <v-card class="ma-2 pa-5">
      <v-card-title>조직활성화비</v-card-title>
      <!-- <v-data-table :items="opr" :headers="headers" disable-pagination hide-default-footer dense></v-data-table> -->
      <v-simple-table>
        <thead>
          <tr>
            <th rowspan="2">구분</th>
            <th v-for="mon in Math.max(...opr.map(e=>e.month))" colspan="6" :key="mon">{{mon}}월</th>
            <th colspan="5" class="sumHead">총계</th>
          </tr>
          <tr>
            <template v-for="mon in Math.max(...opr.map(e=>e.month))" colspan="6">
              <th :key="`${mon}-인원`">인원</th>
              <th :key="`${mon}-예산`">예산</th>
              <th :key="`${mon}-지출`">지출</th>
              <th :key="`${mon}-잔액`">잔액</th>
              <th :key="`${mon}-집행률`">집행률</th>
              <th :key="`${mon}-비고`">비고</th>
            </template>
            <th>인원</th>
            <th>예산</th>
            <th>지출</th>
            <th>잔액</th>
            <th>집행률</th>
          </tr>
        </thead>
        <tbody>
          <template v-for="dep in keys">
            <tr :key="`${dep}`">
              <template v-for="mon in 12">
                <template
                  class="sumHead"
                  v-for="item in opr.filter(e=>e.month==mon&&e.departmentName==dep)"
                >
                  <td v-if="mon==1" :key="`${mon}-${item}-부서`">{{item.departmentName}}</td>
                  <td :key="`${mon}-${item}-인원`">{{item.number}}</td>
                  <td :key="`${mon}-${item}-예산`">{{item.budget}}</td>
                  <td :key="`${mon}-${item}-지출`">{{item.amount}}</td>
                  <td :key="`${mon}-${item}-잔액`">{{item.balance}}</td>
                  <td :key="`${mon}-${item}-집행률`">{{(item.amount/item.budget).toFixed(2)}}%</td>
                  <td :key="`${mon}-${item}-비고`">-</td>
                </template>
              </template>
              <td
                :key="`${dep}-총인원`"
              >{{opr.filter(e=>e.departmentName==dep).map(e=>e.number).reduce((t,a)=>t+a,0)}}</td>
              <td
                :key="`${dep}-총예산`"
              >{{opr.filter(e=>e.departmentName==dep).map(e=>e.budget).reduce((t,a)=>t+a,0)}}</td>
              <td
                :key="`${dep}-총지출`"
              >{{opr.filter(e=>e.departmentName==dep).map(e=>e.amount).reduce((t,a)=>t+a,0)}}</td>
              <td
                :key="`${dep}-총잔액`"
              >{{opr.filter(e=>e.departmentName==dep).map(e=>e.balance).reduce((t,a)=>t+a,0)}}</td>
              <td :key="`${dep}-총집행률`">총 %</td>
            </tr>
          </template>
        </tbody>
      </v-simple-table>
    </v-card>
  </v-container>
</template>

<script>
import { data } from "@/mock/operatingMock";
export default {
  data() {
    return {
      opr: [],
      keys: [],
      year: new Date().getFullYear(),
      headers: [
        { text: "월", value: "month", align: "center", class: "head" },
        { text: "분기", value: "quarter", align: "center", class: "head" },
        {
          text: "부서명",
          value: "departmentName",
          align: "center",
          class: "head"
        },
        { text: "예산", value: "budget", align: "center", class: "head" },
        { text: "소비", value: "amount", align: "center", class: "head" },
        { text: "잔금", value: "balance", align: "center", class: "head" }
      ]
    };
  },
  created() {
    this.opr = data;
    this.keys = [...new Set(this.opr.map(e => e.departmentName))];
  }
};
</script>

<style lang="scss" scoped>
th,
tr,
td {
  border: solid 1px #f1f1f1 !important;
}
.sumHead {
  background-color: #fff0f2 !important;
}
</style>